body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.p-card {
  margin: 20px;
}

.p-card-image {
  border-radius: 10px;
  width: 300px;
  height: 480px;
  box-shadow: 0px 0px 3px 1px #ccc;
}

.p-card-info {
  margin-top: 10px;
  min-height: 100px;
}

.p-card-title {
  font-size: 24px;
  margin: 0px;
}

.p-card-sub-title {
  font-size: 16px;
  margin: 0px;
}

.succes-msg {
  color: #57A773;
  padding: 10px 15px;
}

.err-msg {
  color: #EE6352;
  padding: 10px 15px;
}

.vr46 {
  color: #ffdd00;
  font-size: 30px;
  font-weight: bold;
}

.vr46:hover {
  color: #ffdd00;
}

@media screen (max-width: 500px) {
  .titlesize {
    font-size: 10px;
  }
}